

import React, { lazy,Suspense,Component } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import {
  CHeader,
  CToggler,
  CHeaderBrand,
  CHeaderNav,
  CBadge,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CProgress,
  CRow,
  CCallout
} from '@coreui/react'
import {
  Card,
  Col,
  Container,
  Row,
  Spinner,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
  ListGroup,
  ListGroupItem,
} from "reactstrap"
import CIcon from '@coreui/icons-react'
import imgResult from '../../../src/assets/img/portfolio/portfolio-2.jpg'
import Carousel from 'react-bootstrap/Carousel'

import { CContainer, CFade } from '@coreui/react'
import  { Redirect } from 'react-router-dom'
import conf from '../../conf.js';
// import conf from '../../conf.js';
import { storage } from "../../firebase/firebase";
import $ from 'jquery'
import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
import TheFooter from '../../containers/TheFooter'
import logo from '../../../src/assets/ot.png'


const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)


class NosResultats extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
        page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
        <Spinner type="grow" color="success" /></div>,
        resultats:[],
        tournoi:[],
        status:true
      }
      this.handleFile.bind(this)
  }
  
  componentDidMount() {
    const requestOptions = {
      method: 'GET',
     
    };

    fetch(conf.url+"tournoi/tournois",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data tournoi===>",data)
      this.setState({status:false})
      try{
        this.setState({tournoi:data,status:false})
 
       }catch(error){
        this.setState({tournoi:[]})
       }
     }
     
     );


  };

  handleFile=(file)=>{
    var link=""
     storage.ref("images")
                .child(file).getDownloadURL()
                .then((url) => {
                  
                    link=url
                })
    return  link;

  }



  render() {

    return (
      <>
        {this.state.ok}
        <CContainer fluid>
    <Suspense fallback={loading}>
  {
    (!this.state.status)?

 (
    <CFade>
   
      <div className="mt-5"></div>

      <section id="contact" class="contact">
        <div class="container">



          {/* Tournoi inter-écoles des écoles secondaires */}

          <div className="mt-5">
            <div class="section-title">
              <h2>Resultats des tournois</h2>
            </div>
          </div>

          <Row> 
            <Card>
              
              <CardBody>
                
                {/* <CardTitle tag="h5">{element.titre}</CardTitle>
                <CardSubtitle tag="h6" className="mb-2 text-muted">bouton: {element.bouton}</CardSubtitle>
                <CardText>{element.description}</CardText> */}

                {
                  this.state.tournoi.length?
                  (this.state.tournoi.map((element)=>(
                  <Row className="mt-4">
                  <Col> 
                    
                    <div   role="form" class="php-email-form" >
                      <h2 className="text-center mb-4">{element.nom}{element.typeTournoi=="TOURNOI_INTER_ENTREPRISE"?"/ Tournoi inter entreprise":"Tournoi inter école"}</h2>
                      <hr className="text-center mb-5"></hr>
                      <div className="form-row text-center">
                        {
                       (element.matchs.length)?
                        element.matchs.map((equipe)=>( 
                        (equipe.repousse=="JOUE")?   
                        <div class="form-group col-md-6">

                          <ListGroup>
                            <ListGroupItem action variant="secondary" className="mb-2">
                              <Row>
                                <Col>
                                  <Row>
                                    <p className=" float-left">{equipe.equipeVisites[0].nomEquipe}</p>
                                  </Row>
                                  <Row>
                                    <img src={logo} class="img-fluid" alt="" width="20" height="20"/>
                                  </Row>
                                </Col>
                                <Col>
                                  <center> 
                                    <p style={{textAlign:"center"}}> 
                                      <strong>{equipe.scoreVisite}</strong>
                                      - 
                                      <strong> {equipe.scoreVisiteur} </strong>
                                    </p>
                                  </center>
                                  <center> <small style={{textAlign:"center"}}> {equipe.date}</small> </center>
                                  <center> <small style={{textAlign:"center"}}> {equipe.heure} </small> </center>
                                </Col>
                                <Col>
                                  <Row>
                                    <p className=" float-left">{equipe.scoreVisiteur}</p>
                                  </Row>
                                  <Row>
                                    <img src={logo} class="img-fluid" alt="" width="20" height="20"/>
                                  </Row>
                                </Col>
                              </Row>
                            </ListGroupItem>
                          </ListGroup>
                         

                        </div>:<h4 className="text-center" style={{textAlign:"center"}}>Aucun résultat disponible pour ce tournoi</h4>)):<h4 className="text-center" style={{textAlign:"center"}}>Aucun résultat disponible pour ce tournoi</h4>
                        }

                        
                      </div>
                    </div>
                  </Col>
                </Row>
                ))):null
                  }
              </CardBody>
            </Card>
          </Row>

        </div>
      </section>
    
    </CFade>):(<>{this.state.page}</>)
     }
    </Suspense>
    </CContainer>
      </>
    )
  }
}

export default NosResultats
